@charset "UTF-8";
:root {
  --clip: left center;
  --size: 0;
  --background-thumb: #2b3769;
  --scrollbar-thumb-bgcolor: #757575;
  --stripe-width: 10px;
  --nav-marker-border: #3b8686;
  --sidebar-width: 28.5rem;
  --duration: 0.3s;
  --button-size: 4rem;
  --mob-expanded-width: 320px;
  --mob-collapsed-width: 4rem;
}

.masked-input {
  color: transparent; /* Скрыть текст, пока маска не активна */
}

.masked-input::placeholder {
  color: black; /* Цвет плейсхолдера */
}

.gradient-preloader {
  background: linear-gradient(90deg, #2b3769 -23.739%, #93a9ff 96.778%);
}

@media (min-width: 1440.99px) {
  .underline:hover {
    font-weight: 600;
    opacity: 1;
  }
  .underline:hover::before {
    width: 47%;
  }
  .underline:hover::after {
    width: 47%;
  }
  .underline::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0px;
    width: 0;
    height: 2px;
    background-color: #000;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0px;
    width: 0;
    height: 2px;
    background-color: #000;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline-w:hover {
    font-weight: 600;
    opacity: 1;
  }
  .underline-w:hover::before {
    width: 47%;
  }
  .underline-w:hover::after {
    width: 47%;
  }
  .underline-w::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0px;
    width: 0;
    height: 2px;
    background-color: #fff;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline-w::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0px;
    width: 0;
    height: 2px;
    background-color: #fff;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
}
.performance {
  position: relative;
  overflow: hidden;
  width: 100%;
  background: #171b1c;
}
.performance__wrapper {
  background: url("../img/performance/ellipse-01.png") top -15% left 17%/50% no-repeat, url("../img/performance/ellipse-02.png") top -6% right -7%/25% no-repeat;
}
.performance__wrapper::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  background: radial-gradient(ellipse 120% 50% at 30% 10%, rgba(23, 27, 28, 0.74) 0%, rgba(23, 27, 28, 0.397) 25%, transparent 65%);
}
@media (min-width: 320.98px) and (max-width: 1440px) {
  .performance__wrapper {
    background: url("../img/performance/ellipse-01.png") top calc(45% + -60 * (100vw - 690px) / 750) left calc(-235px + 85 * (100vw - 690px) / 750)/calc(600px + 250 * (100vw - 690px) / 750) no-repeat, url("../img/performance/ellipse-02.png") top -6% right calc(-150px + 0 * (100vw - 690px) / 750)/calc(55% + -320 * (100vw - 690px) / 750) no-repeat;
  }
}
@media screen and (max-width: 320px) {
  .performance__wrapper {
    background: url("../img/performance/ellipse-01.png") top 48% left -290px/490px no-repeat, url("../img/performance/ellipse-02.png") top -6% right -150px/330px no-repeat;
  }
}
.performance__g-box {
  position: absolute;
  width: 100%;
  height: 100%;
}
.performance__g-box::before, .performance__g-box::after {
  content: "";
  position: absolute;
  width: 250%;
  height: 250%;
  pointer-events: none;
}
.performance__g-box::after {
  left: -40%;
  top: 0;
  z-index: 3;
  background: radial-gradient(ellipse 70% 40% at 40% 50%, rgb(43, 55, 105) 10%, rgba(43, 55, 105, 0.795) 30%, transparent 60%);
}
.performance__g-box::before {
  top: -40%;
  left: -50%;
  z-index: 4;
  background: radial-gradient(circle at 20% 49%, rgba(110, 123, 177, 0.623) 1%, rgba(43, 55, 105, 0.26) 15%, transparent 25%), radial-gradient(circle at 64% 35%, rgba(110, 123, 177, 0.623) 1%, rgba(43, 55, 105, 0.26) 15%, transparent 35%);
}
.performance__body {
  padding-top: 21.6rem;
  padding-bottom: 14.7rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 60px;
  height: 100%;
  width: 100%;
  overflow: hidden;
}
@media (min-width: 1440.98px) and (max-width: 1920px) {
  .performance__body {
    column-gap: calc(20px + 40 * (100vw - 1440px) / 480);
  }
}
@media screen and (max-width: 1440px) {
  .performance__body {
    padding: 21.6rem 30px 14.7rem;
    gap: 20px;
  }
}
@media screen and (max-width: 690px) {
  .performance__body {
    padding: 100px 30px 60px;
  }
}
@media screen and (max-width: 375px) {
  .performance__body {
    padding: 100px 10px 60px;
  }
}
.performance__row {
  width: 100%;
  position: relative;
  z-index: 9;
}
.performance__row:nth-child(1) {
  grid-column: 1/4;
  max-width: 950px;
}
.performance__row:nth-child(2) {
  margin-bottom: 115px;
  grid-column: 2/4;
  display: flex;
  flex-direction: column;
  align-self: start;
  max-width: 950px;
}
@media screen and (max-width: 960px) {
  .performance__row:nth-child(2) {
    margin-left: 0;
    grid-column: 1/4;
  }
}
@media screen and (max-width: 690px) {
  .performance__row:nth-child(2) {
    margin-bottom: 60px;
  }
}
@media screen and (max-width: 490px) {
  .performance__row:nth-child(2) {
    transform: translateX(0);
  }
}
.performance__row:nth-child(3) {
  grid-column: 1/4;
}
.performance__title {
  color: #fff;
  font-weight: 400;
}
.performance__title h1 {
  margin-bottom: 24px;
  line-height: 100%;
}
@media screen and (max-width: 690px) {
  .performance__title h1 {
    margin-bottom: 40px;
    text-align: center;
  }
}
.performance__title h2 {
  font-size: 55px;
  line-height: 100%;
}
@media (min-width: 320.98px) and (max-width: 1920px) {
  .performance__title h2 {
    font-size: calc(16px + 39 * (100vw - 320px) / 1600);
  }
}
@media screen and (max-width: 320px) {
  .performance__title h2 {
    font-size: 16px;
  }
}
@media (min-width: 1640.99px) {
  .performance__title {
    position: relative;
  }
}
@media screen and (max-width: 1640px) {
  .performance__title {
    display: flex;
    flex-direction: column;
  }
}
.performance__text {
  position: relative;
  align-self: end;
  margin: 100px 0;
}
@media (min-width: 1440.98px) and (max-width: 1920px) {
  .performance__text {
    margin: calc(50px + 50 * (100vw - 1440px) / 480) 0;
  }
}
.performance__text p {
  color: #fff;
  line-height: 125%; /* 20px/16 */
}
.performance__list {
  color: #fff;
  margin-left: 19px;
}
.performance__item {
  list-style: disc;
}
.performance__item span {
  margin-left: 20px;
  line-height: 150%; /* 24px/16 */
}
@media screen and (max-width: 490px) {
  .performance__item span {
    margin-left: 0px;
  }
}
.performance__button {
  right: 0;
  bottom: 0;
  background: #ffffff;
  border: 1px solid transparent;
  align-self: flex-start;
}
.performance__button:hover {
  border: 1px solid #fff;
}
.performance__button:hover .icon {
  transform: rotate(-45deg);
}
.performance__button:hover span {
  color: #fff !important;
}
.performance__button:hover::before {
  background: #2b3769;
}
@media screen and (max-width: 960px) {
  .performance__button {
    align-self: center;
  }
}
.performance .icon-arrow-right {
  color: #fff;
}

.bottom-menu__items {
  display: grid;
  width: 100%;
  grid-template-columns: repeat(3, 1fr);
  gap: 60px;
}
@media (min-width: 1440.98px) and (max-width: 1920px) {
  .bottom-menu__items {
    gap: calc(20px + 40 * (100vw - 1440px) / 480);
  }
}
@media screen and (max-width: 1440px) {
  .bottom-menu__items {
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }
}
@media screen and (max-width: 890px) {
  .bottom-menu__items {
    grid-template-columns: repeat(1, 1fr);
    justify-items: center;
  }
}
.bottom-menu__button {
  background: url("../img/performance/button-bg.png") center/cover no-repeat;
  min-height: 94px;
  width: 100%;
  max-width: 100%;
  padding: 0 30px;
  overflow: hidden;
}
.bottom-menu__button:hover span {
  opacity: 0.7;
  color: #000;
}
.bottom-menu__button:hover .icon {
  transform: rotate(-45deg);
}
.bottom-menu__button span {
  margin: 0;
  transition: opacity 0.4s ease-in-out;
}
@media screen and (max-width: 890px) {
  .bottom-menu__button {
    max-width: 480px;
    padding: 0 15px;
  }
}
@media screen and (max-width: 690px) {
  .bottom-menu__button {
    min-height: 65px;
  }
}
.bottom-menu__item {
  display: flex;
  align-items: center;
}